var Bmob = require('./bmob');

var fetch = "xxrequire('@system.fetch')xx";

var setHeader = function setHeader(config) {
  var header = {
    'content-type': 'application/json',
    'X-Bmob-SDK-Type': 'wechatApp',
    'X-Bmob-Application-Id': config.applicationId,
    'X-Bmob-REST-API-Key': config.applicationKey
  };

  if (config.applicationMasterKey) {
    header['X-Bmob-Master-Key'] = config.applicationMasterKey;
  }

  return header;
};

var request = function request(route) {
  var method = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 'get';
  var parma = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : {};
  return new Promise(function (resolve, reject) {
    var header = setHeader(Bmob._config);

    if (undefined === Bmob.User) {
      Bmob = require('./bmob');
    }

    var current = Bmob.User.current();

    if (current) {
      header['X-Bmob-Session-Token'] = current.sessionToken;
    }

    if (typeof parma === 'object') {
      parma = JSON.stringify(parma);
    }

    fetch.fetch({
      url: Bmob._config.host + route,
      header: header,
      method: method,
      data: parma,
      success: function success(res) {
        var data = JSON.parse(res.data);

        if (data.code) {
          reject(data);
        }

        resolve(data);
      },
      fail: function fail(data, code) {
        console.log("handling fail, code = ".concat(code));
        reject(data);
      }
    });
  });
};

module.exports = request;